
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState } from 'vuex'
export default {
name: 'page',
directives: {
TransferDom
},
data() {
return {
showMenu: false,
menus: {
menu1: '关于 Pretty Vendor ',
menu2: '喜欢就给我个starb吧 ^_^ ',
menu3: '退出登录'
}
}
},
created() {},
computed: {
...mapState(['title']),
leftOptions() {
return {
showBack: this.$route.path !== '/page/home'
}
},
rightOptions() {
return {
showMore: true
}
},
isShowTabbar() {
if (/detail|theme|order|address|about/.test(this.$route.path)) {
return true
}
return false
},
isPaddingBottom() {
if (/detail|theme|order|address|about/.test(this.$route.path)) {
return '0'
}
return '53px'
}
},
methods: {
onClickMore() {
this.showMenu = true
},
onSelectMenu(key, item) {
if (key === 'menu1') {
this.$router.push({ path: '/page/about' })
} else if (key === 'menu2') {
window.location.href =
'https://huahua0406.github.io/pretty-vendor/'
} else if (key === 'menu3') {
this.$router.push({ path: '/login' })
}
}
},
components: {
Tabbar,
TabbarItem,
ViewBox,
XHeader,
Actionsheet,
TransferDom
}
}



